
.sticky{
    position: sticky;
    top: 0;
    z-index: 2;

}
.nav{
   font: optional;
    /* padding: 40px 50px;
    display: flex;
    justify-content: space-around;
    position: fixed;
    width: 100%;
    z-index: 2; */
}
.navlinks>Button{
    font-size: 22px;
   border: none;
   padding: 10px 20px;
   font-weight: bolder;
   font-family: sans-serif;

  
}
.navlinks>Button:hover{
    cursor: pointer;
}

.content h1{
    font-size:80px;
    
  
   margin: 5px 50px;
}
.content h2{
    font-size: 42px;
    font-weight: bold;
}
.content>div:nth-child(2){
    display: flex;
    justify-content: center;
    align-items:flex-end;
    font-size: 22px;
}


.intro{
    
    display: flex;
    justify-content: space-evenly;
    
   
    
   
}
.projectcontainer{
    width: 70%;
   
    margin: 10px auto;
}
.project{
    
  
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
}

.project>div:nth-child(1){
    flex:7;
}
.project>div>img{
    width: 90%;
    height: 200px;
}
.project>div:nth-child(2){
    flex:3;
    text-align: left;
    font-family: sans-serif;
   margin-top: -10px;
   padding: 5px;
}.project>div:nth-child(2)>a>button{
    background-color: teal;
    margin: 5px;
    color: white;
    font-weight: bold;
    border-radius: 10px;
    padding: 10px;
}
.project>div:nth-child(2)>a>button:hover{
    cursor: pointer;
    background-color: wheat;
    color: black;
}
.project>div:nth-child(2)>p>button{
    background-color: rgb(32, 110, 189);
    color: white;
    margin: 5px;
    font-weight: bold;
    border-radius: 5px;
    padding: 5px 10px;
}
.project>div:nth-child(2)>p>button:hover{
   cursor:pointer;
   background-color: wheat;
   color: black;
}
