    .frontendskills {
      width: 50rem;
      min-width: 40rem;
      max-width: 75%;
      margin: 3rem 0;
    }
    

.skillcontainer {
  max-width: 40rem;
  margin: 0 auto;
  background-color:white;
  border-radius: 3px;
  margin-bottom: 1rem;
  opacity: .95;
  transition: all .5s ease;
  
}
.skillcontainer:hover{
    opacity: 80%;
}

.skills {
  padding: .25rem 1rem;  
  font-size: 22px;
  font-weight: 100;
  text-align: right;
  border-radius: 3px;
  color: white;
  background-color: #be3144;
  @extend .unselectable
}

.htmling {width: 75%; background-color: #be3144;}
.css {width: 75%; background-color: #3157be;}
.js {width: 60%; background-color: #44be31;}
.react {width: 40%; background-color:  #a157be;}
   
.contact{
   
   font-size: 20px;
    display: flex;
    justify-content: space-around;
    width: 70%;
    margin: 20px auto 50px auto;
}
.contact>div:nth-child(2){
   display: flex;
   gap: 20px;
}
.contact>div:nth-child(2)>a {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.contact>div:nth-child(1)>p{
    display: flex;
    justify-content:left;
    align-items: center;
    gap: 10px;
}


@media screen and (min-width: 801px) and (max-width:1200px){
    
    .content h1{
        font-size:70px;
        
      
        margin: 10px 30px;
    }
    .intro{
       
        display: grid;
        grid-template-columns: repeat(1,1fr);
        
       padding: 10px 50px;
        
       
    }
    .projectcontainer{
        width: 90%;
       
       
    }
    .contact{
   
    
     
         justify-content: space-between;
         width: 80%;
        
     }
    
    

}

@media screen and (min-width: 380px) and (max-width:800px){
    .navlinks>Button{
        font-size: 18px;
        padding: 10px 10px;
    
      
    }
    .content h1{
        font-size:50px;
        
      
        margin: 5px 25px;
    }
    .intro{
       
        display: grid;
        grid-template-columns: repeat(1,1fr);
        
       padding: 10px 50px;
        
       
    }
    .projectcontainer{
        width: 100%;
       
        
    }
    .project{
        
      
        display: grid;
        grid-template-columns: repeat(1,1fr);
    }
    .project>div>img{
        width: 100%;
        
    }
    .contact{
   
       
         display: grid;
        grid-template-columns: repeat(1,1fr);
      
        
     }
}